<template>
  <!-- v-show="shareOptionsVisible" -->
  <div class="shareOptions">
    <div class="shareOptionsInner">
      <div class="facebookMobile" @click="navTo(facebookShare)">
        <facebookLogo />
      </div>
      <div class="twitterMobile" @click="navTo(twitterShare)">
        <twitterLogo />
      </div>
    </div>
    <div class="shareOptionsUnderlay"></div>
  </div>
</template>

<script>
  import facebookLogo from '@/components/facebookLogo'
  import twitterLogo from '@/components/twitterLogo'

  export default {
    // props: ["facebookShare", "twitterShare"],
    data() {
      return {
        facebookShare: "https://www.facebook.com/sharer/sharer.php?u=https%3A//www.refinery29.com/bold-makeup-beauty-looks-video/",
        twitterShare: "https://twitter.com/home?status=Bold,%20Colorful%20Makeup%20Looks%20Women%20Love%20To%20Wear%20https%3A//www.refinery29.com/bold-makeup-beauty-looks-video/"
      }
    },
    components: {
      facebookLogo,
      twitterLogo
    },
    methods: {
      navTo(url) {
        if (url) window.open(url, '_blank')
      },
    }
  }

</script>

<style scoped lang="scss">
  @import '../scss/variables'; // todo: move to component
  .shareOptions {
    position: absolute;
    box-sizing: border-box;
    top: 0;
    right: 0;
    margin-top: 69px;
    margin-right: 5px;
    width: 120px;
    height: 65px;
    background: $white;
    border: 2px solid $black;
    z-index: 1;
    overflow: visible;
    cursor: pointer;
    &::after {
      content: ' ';
      position: absolute;
      z-index: 2;
      top: 0;
      right: 0;
      width: 0px;
      height: 0px;
      margin-top: -10px;
      margin-right: 16px;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 10px solid $white;
    }
    &::before {
      content: ' ';
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      width: 0px;
      height: 0px;
      margin-top: -14px;
      margin-right: 14px;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      border-bottom: 12px solid $black;
      transform: scale(0.9, 0.9) translateY(5%);
      transform-origin: center;
    }
    @media screen and (min-width: $desktop-min) {
      display: none;
    }
    >.shareOptionsInner {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
    }

    .facebookMobile,
    .twitterMobile {
      position: relative;
      height: 30px;
      width: 30px;
      top: 50%;
      transform: translateY(-50%);
      margin-left: 12px;
      margin-right: 12px;
      overflow: hidden; // background: $light-red;
      >svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

</style>



