<template>
  <div class="sidebar">
    <p :class="isWhite">live boldly road trip</p>
    <div class="refineryLogo" @click="navTo('http://www.refinery29.com/', 'r29 logo')">
      <refineryLogo :white="white" />
    </div>
    <div class="facebookLogo" @click="navTo(facebookShare, 'facebook')">
      <facebookLogo :white="white" />
    </div>
    <div class="twitterLogo" @click="navTo(twitterShare, 'twitter')">
      <twitterLogo :white="white" />
    </div>

    <div class="shareIcon" @click="toggleShareOptions">
      <shareIcon />
    </div>

    <!-- todo: make component -->
    <!-- shareoptions -->
    <shareOptions v-show="shareOptionsVisible" />

  </div>
</template>

<script>
  import refineryLogo from '@/components/refineryLogo'
  import twitterLogo from '@/components/twitterLogo'
  import facebookLogo from '@/components/facebookLogo'
  import shareIcon from '@/components/shareIcon'
  import shareOptions from '@/components/shareOptions'

  export default {
    props: ["white"],
    data() {
      return {
        shareOptionsVisible: false,
        facebookShare: "https://www.facebook.com/sharer/sharer.php?u=https%3A//www.refinery29.com/bold-makeup-beauty-looks-video/",
        twitterShare: "https://twitter.com/home?status=Bold,%20Colorful%20Makeup%20Looks%20Women%20Love%20To%20Wear%20https%3A//www.refinery29.com/bold-makeup-beauty-looks-video/"
      }
    },
    computed: {
      isWhite() {
        return this.white ? 'white' : ''
      }
    },
    methods: {
      navTo(url, gtagString) {
        if (url) window.open(url, '_blank')
        gtag('event', 'clicked', {
          'event_label': gtagString,
          'event_category': 'Bespoke - Revlon Map'
        })
      },
      toggleShareOptions() {
        this.shareOptionsVisible = this.shareOptionsVisible ? false : true
      }
    },
    components: {
      refineryLogo,
      facebookLogo,
      twitterLogo,
      shareIcon,
      shareOptions
    }
  }

</script>

<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/fonts';

  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    margin-left: 24px;
    width: 36px;
    height: 100%;
    z-index: 2; // background: rgba(255, 0, 0, 0.25);
    overflow: hidden;
    @media screen and (max-width: $tablet-max) {
      height: 65px;
      width: 100%;
      background: $black;
      margin-left: 0px;
      z-index: 0;
      overflow: visible; // todo: add higher z-index here and on video sidebar
      // TODO: get rid of this later
      z-index: 5;
    }
  }

  p {
    // https://stackoverflow.com/questions/15138723/vertically-center-rotated-text-with-css
    @include p1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    text-transform: uppercase;
    word-wrap: none;
    width: 250px; // background: rgba(255, 0, 0, 0.25);
    text-align: center;
    user-select: none;
    @media screen and (max-width: $tablet-max) {
      top: 0;
      left: 0;
      transform: none;
      color: white;
      top: 50%;
      transform: translateY(-50%); // margin-left: 85px;
      text-align: center;
      width: 100%;
      pointer-events: none;
    }
    &.white {
      color: white;
    }
  }

  .refineryLogo {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 20px;
    width: 100%;
    height: 45px; // background: rgba(255, 0, 0, 0.25);
    @media screen and (max-width: $tablet-max) {
      margin-top: 0px;
      top: 50%;
      transform: translateY(-50%);
      // width: 45px;
      // margin-left: 20px;
      width: 35px;
      margin-left: 15px;
      >svg {
        fill: white;
      }
    }
  }

  .twitterLogo {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 24px;
    margin-bottom: 20px;
  }

  .facebookLogo {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 24px;
    margin-bottom: 64px;
  }

  .twitterLogo,
  .facebookLogo {
    @media screen and (max-width: $tablet-max) {
      display: none;
    }
  }

  .shareIcon {
    position: absolute;
    top: 0;
    right: 0;
    // width: 60px;
    width: 45px;
    height: 100%;
    margin-right: 10px;
    @media screen and (min-width: $desktop-min) {
      display: none;
    }
  }

  .facebookLogo,
  .twitterLogo,
  .refineryLogo,
  .shareIcon {
    cursor: pointer;
    >svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.8;
      transition: opacity 0.3s ease-in-out;
      @media screen and (max-width: $tablet-max) {
        opacity: 1;
      }
    }
    &:hover {
      >svg {
        opacity: 1;
      }
    }
  }

</style>



