<template>
  <div class="swiper-container map-swiper">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(item, index) in list" v-bind:key="index" :slideIndex="index" @click="showInfluencer(item)"></div>
    </div>
  </div>
</template>

<script>
  import swiper from 'Swiper'
  export default {
    props: ['list'],
    data() {
      return {
        swiper: null,
        activeIndex: 0,
        // influencerName: ""
      }
    },
    methods: {
      showInfluencer(item) {
        this.$emit('showInfluencer', item)
      }
    },
    mounted() {

    },
    watch: {
      // todo: bind other swiper actions to this swiper also
      list() {
        let ctx = this

        // init swiper on list populate
        setTimeout(() => {
          this.swiper = new swiper('.map-swiper', {
            slideActiveClass: 'map-slide-active',
            on: {
              slideChangeTransitionStart() {
                ctx.activeIndex = document.querySelector('.map-slide-active').getAttribute('slideIndex')
                // ctx.influencerName = document.querySelector('.map-slide-active').getAttribute('influencerName')
                ctx.$emit('changeSlideIndex', ctx.activeIndex)
              }
            }
          })
        })

      }
    }
  }

</script>

<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/swiper';

  .map-swiper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin-top: 65px;
    z-index: 1;
    cursor: pointer;
    @media screen and (min-width: $tablet-min) {
      display: none;
    }
  }

</style>



