<template>
  <div class="revlonStack" @click="navTo(revlonUrl)">
    <span>paid for by</span>
    <div class="revlon">
      <revlonLogo />
    </div>
    <span class="liveBoldly">
      live boldly
    </span>
  </div>
</template>

<script>
  import revlonLogo from './revlonLogo'
  export default {
    data() {
      return {
        revlonUrl: "http://www.revlon.com/live-boldly"
      }
    },
    methods: {
      navTo(url) {
        if (url) window.open(url, '_blank')
      },
    },
    components: {
      revlonLogo
    },
  }

</script>

<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/fonts';

  .revlonStack {
    position: relative;
    font-size: 11px;
    letter-spacing: 2.2px;
    font-family: "BrownStd";
    color: $black;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;
    >.liveBoldly {
      @include live-boldly;
    }
  }

  .revlon {
    position: relative;
    display: inline-block;
    vertical-align: bottom;
    height: 24px;
    width: 96px;
    padding-left: 6px;
    >svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      transform: translateY(-10px) scale(2, 2)
    }
  }

</style>



