<template>
  <div class="wrapper">

    <mapContainer v-show="true" :mapTranslateClass="mapTranslateClass" @changeSlideIndex="changeSlideIndex" @showInfluencer="showInfluencer"
      :customClass="mapCustomClass" :list="influencers" />
    <influencer v-show="true" @hideInfluencer="hideInfluencer" :content="influencerContent" :customClass="influencerClass" />
    <list @showInfluencer="showInfluencer" @hideInfluencer="hideInfluencer" @changeMapTranslateClass="changeMapTranslateClass"
      :list="influencers" :slideIndex="slideIndex" :influencerVisible="influencerVisible" :listAnimationClass="listAnimationClass"
    />
    <sidebar />
    <landing @jumpIn="jumpIn" :landingWatcher="landingWatcher" />

  </div>
</template>

<script>
  // notes
  // 1280x800 board

  import initContentful from '@/contentful'
  import "babel-polyfill" // IE11
  import axios from 'axios'

  import usMap from '@/components/usMap'
  import arrowRight from '@/components/arrowRight'
  import sidebar from '@/components/sidebar'
  import list from '@/components/list'
  import mapContainer from '@/components/mapContainer'
  import influencer from '@/components/influencer'
  import landing from '@/components/landing'

  // helper method
  const getMapEl = (location, options) => {
    options = options || {}

    let commaInd = location.indexOf(", ")
    let mapState = location.slice(commaInd).replace(", ", "")
    if (commaInd === -1) mapState = location.toLowerCase().split(' ').join('')
    let mapEl = document.querySelector('.' + mapState)

    if (options.stringOnly) return mapState

    return mapEl
  }

  const eastStates = ["ME", "NH", "VT", "MA", "RI", "CT", "NY", "PA", "DE", "MD", "WV", "VA", "NC", "SC", "GA", "FL",
    "OH", "KY", "TN", "AL", "MS", "MI", "IL", "IN"
  ]

  export default {
    data() {
      return {
        influencers: new Array,
        influencerContent: {
          status: "live"
        },
        influencerVisible: false,
        slideIndex: 0,
        mapTranslateClass: "",
        mapCustomClass: "",
        influencerClass: "",
        timeouts: [],
        listAnimationClass: "",
        landingWatcher: false
      }
    },
    methods: {
      showInfluencer(item) {

        gtag('event', 'visible', {
          'event_label': item.locationName,
          'event_category': 'Bespoke - Revlon Map'
        })

        let urlString = item.influencerName.split(' ').join('-').toLowerCase()
        this.$router.push(urlString)

        this.influencerVisible = true

        let contentTimeout = 0
        let influencerTimeout = 50

        if (this.influencerClass === "showInfluencer" && window.innerWidth > 995) {
          contentTimeout = 500
          influencerTimeout = 650
        } else if (window.innerWidth < 996) {
          contentTimeout = 0
          influencerTimeout = 0
        }

        for (let i = 0; i < this.timeouts.length; i++) {
          clearTimeout(this.timeouts[i])
        }

        this.influencerClass = ""

        this.timeouts.push(setTimeout(() => {
          this.influencerContent = item
          this.timeouts.push(setTimeout(() => {
            this.influencerClass = "showInfluencer"
          }, influencerTimeout))
        }, contentTimeout))

        let mapState = getMapEl(item.locationName, {
          stringOnly: true
        })
        this.mapCustomClass = "hideMap"
        if (eastStates.indexOf(mapState) !== -1) {
          this.mapTranslateClass = "mapTranslated"
        } else {
          this.mapTranslateClass = ""
        }
      },
      hideInfluencer() {
        this.$router.push('/')
        this.influencerClass = ""
        this.influencerVisible = false
        this.mapCustomClass = ""
      },
      changeMapTranslateClass(newClass) {
        this.mapTranslateClass = newClass
      },
      changeSlideIndex(index) {
        this.slideIndex = index
      },
      jumpIn() {
        this.listAnimationClass = "hasAnimation"
      }
    },
    mounted() {
      initContentful(this, 'influencers')
    },
    watch: {
      influencers() {

        // map states to list items

        for (var i = 0; i < this.influencers.length; i++) {
          let status = this.influencers[i].status
          let location = this.influencers[i].locationName
          let influencerName = this.influencers[i].influencerName.split(' ').join('-').toLowerCase()
          let mapEl = getMapEl(location)
          if (mapEl) {

            // temporary fix
            if (location.toLowerCase().indexOf('luis') !== -1) {
              location = 'Fullerton, CA'
            }

            mapEl.setAttribute('data-status', status)
            mapEl.setAttribute('data-mapLocation', location)
            mapEl.setAttribute('data-maproutename', influencerName)
            mapEl.onclick = () => {
              let listEl = document.querySelector("[data-listLocation='" + location + "']")
              if (!listEl) return

              listEl.click()
              // move swiper to element position too
              this.slideIndex = listEl.getAttribute('data-slideIndex')
            }


          }
        }

        // check for url after content is populated
        let routePath = this.$route.path.replace('/', '')
        if (!!routePath) {
          // open influencer if exists
          setTimeout(() => {
            let listItem = document.querySelector("[data-maproutename='" + routePath + "']")

            if (listItem) {
              this.landingWatcher = true // removed
              // use onclick instead of click for SVG elements
              listItem.onclick()
            }
          }, 1000)
        }


      }
    },
    components: {
      usMap,
      arrowRight,
      sidebar,
      list,
      mapContainer,
      influencer,
      landing
    }
  }

</script>

<style lang="scss">
  @import '../scss/fonts';
  @import '../scss/aspectRatio';
  @import '../scss/variables';

  html,
  body {
    margin: 0px;
    background: $red;
    ::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, .2);
    }
    ::-webkit-scrollbar {
      background-color: transparent;
    }
  }

  .wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh; // background: rgba(255, 0, 0, 0.15);
    @media screen and (max-width: $tablet-max) {
      position: relative;
      padding: 0px;
      margin: 0px; // height: initial;
    }
  }

</style>



