import * as contentful from 'contentful'
import keys from './keys'

const client = contentful.createClient(keys)

export default (context, data) => {
  client.getEntries({
    'content_type': 'mapContent',
    include: 5
  })
  
  .then((entries) => {
    context[data] = entries.items.map((item) => {
      return item.fields
    }).sort((a, b) => {
      return a.locationId - b.locationId
    })
  }) 
}


