<template>
  <div class="influencer-container" :class="[comingSoon, customClass, hasVideoClass]">


    <transition name="fade">
      <div class="content" v-show="content.status === 'live'">
        <div class="influencer">
          <p>{{content.locationName}}</p>
          <div class="image" :class="customClass">
            <div class="influencerImage" :style="influencerImage"></div>
            <div class="playButton" @click="showVideo">
            </div>
          </div>
          <div class="influencer-title">
            <h1 :class="customClass" @click="openInfluencerClickOut">{{content.influencerName}}</h1>
            <div class="shop-button" :class="customClass">
              <span @click="openShopClickOut" @mouseover="openShopClickOutIOS">shop this look</span>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div class="contentComingSoon" v-show="content.status !== 'live'">
        <div>
          <p>{{content.locationName}}</p>
          <h1>{{content.influencerName}}</h1>
          <p>next stop</p>
        </div>
      </div>
    </transition>


    <!-- return to map -->
    <!-- @click="hideInfluencer(true)" -->
    <div class="return-bar" @click="hideInfluencerDesktop">
      <div class="refineryLogo" @click="hideInfluencer">
        <refineryLogo :white="true" />
      </div>
      <p @click="hideInfluencer">{{returnText}}</p>
      <div class="shareIcon" @click="toggleShareOptions" v-show="!videoVisible">
        <shareIcon />
      </div>
      <div class="closeIcon" @click="hideInfluencer" :class="videoVisibleCloseClass">
        <closeIcon />
      </div>
      <div class="shareOptionsContainer">
        <shareOptions v-show="shareOptionsVisible" />
      </div>
    </div>
    <!--  -->

    <transition name="fade">
      <div class="videoContainer" v-show="videoVisible">
        <loader />
        <video class="jwplayer" controls="true" autoplay="true" :src="content.jwVideoObject" ref="videoObject" v-if="videoContentVisible"
        />
        <sidebar white="true" />

      </div>
    </transition>

  </div>
</template>

<script>
  import sidebar from './sidebar'
  import loader from './loader'
  import refineryLogo from './refineryLogo'
  import closeIcon from './closeIcon'
  import shareIcon from './shareIcon'
  import shareOptions from './shareOptions'

  import "babel-polyfill" // IE11
  import axios from 'axios'

  export default {
    props: ["content", "customClass"],
    data() {
      return {
        videoVisible: false,
        videoContentVisible: false,
        returnText: "return to map",
        shareOptionsVisible: false
      }
    },
    methods: {
      hideInfluencer() {

        // console.log('fired with', disableMobile)

        // disableMobile = disableMobile || false

        // if (disableMobile && window.innerWidth < 996) return

        // console.log('made it here')

        if (this.videoVisible) {
          this.videoVisible = false
          // setTimeout(() => {
          //   this.$emit('hideInfluencer')
          // }, 500)
          gtag('event', 'clicked', {
            'event_label': 'video close ' + this.content.locationName,
            'event_category': 'Bespoke - Revlon Map'
          })
        } else {
          gtag('event', 'clicked', {
            'event_label': 'return to map',
            'event_category': 'Bespoke - Revlon Map'
          })
          this.$emit('hideInfluencer')
        }

        // this.$refs.videoObject.pause()
        // this.$refs.videoObject.currentTime = 0
        this.videoVisible = false
        this.videoContentVisible = false
        this.returnText = "return to map"

      },
      hideInfluencerDesktop() {

        // if (window.innerWidth < 996 || this.videoVisible) return
        // this.hideInfluencer()
      },
      toggleShareOptions() {
        this.shareOptionsVisible = this.shareOptionsVisible ? false : true
      },
      // hideVideo () {
      //   this.videoVisible = false
      // },
      requestFullScreen(element) {
        if (element.requestFullscreen)
          element.requestFullscreen();
        else if (element.msRequestFullscreen)
          element.msRequestFullscreen();
        else if (element.mozRequestFullScreen)
          element.mozRequestFullScreen();
        else if (element.webkitRequestFullscreen)
          element.webkitRequestFullscreen();
      },
      showVideo() {
        gtag('event', 'clicked', {
          'event_label': 'video ' + this.content.locationName,
          'event_category': 'Bespoke - Revlon Map'
        })

        this.videoVisible = true

        setTimeout(() => {
          this.videoContentVisible = true
          setTimeout(() => {
            let videoObject = this.$refs.videoObject
            this.requestFullScreen(videoObject)
          })
        }, 1000)

        if (window.innerWidth < 996) {
          this.returnText = "close video"
        } else {
          setTimeout(() => {
            this.returnText = "close video"
          }, 1000)
        }
      },
      openShopClickOut() {
        gtag('event', 'clicked', {
          'event_label': 'shop ' + this.content.locationName,
          'event_category': 'Bespoke - Revlon Map'
        })
        let url = this.content.shopClickOut
        if (url) window.open(url, '_blank')
      },
      openShopClickOutIOS() {
        var iOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
        if (iOS) this.openShopClickOut()
      },
      openInfluencerClickOut() {

        gtag('event', 'clicked', {
          'event_label': 'influencer name ' + this.content.locationName,
          'event_category': 'Bespoke - Revlon Map'
        })

        let url = this.content.influencerClickOutUrl
        if (url) window.open(url, '_blank')
      },
    },
    computed: {
      influencerImage() {
        return 'background-image: url("' + this.content.influencerImageUrl + '")'
      },
      comingSoon() {
        return this.content.status === 'soon' ? 'comingSoon' : ''
      },
      hasVideoClass() {
        return this.videoVisible ? "videoVisible" : ""
      },
      videoVisibleCloseClass() {
        return this.videoVisible ? "videoVisible" : ""
      }
      // returnText () {
      //   return this.videoVisible ? "close video" : "return to map"
      // }
    },
    components: {
      sidebar,
      loader,
      refineryLogo,
      closeIcon,
      shareIcon,
      shareOptions
    }
  }

</script>

<style scoped lang="scss">
  @import '../scss/fonts';
  @import '../scss/aspectRatio';
  @import '../scss/variables';
  @import '../scss/transitions';
  @import '../scss/mixins';

  .influencer-container {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    background: white;
    transition: all 0.6s ease-in-out; // left: 0;
    // z-index: -1;
    z-index: 1;
    transform: translateX(-100%);
    &.videoVisible {
      z-index: 8;
    }
    &.showInfluencer {
      transform: none;
    }
    &.comingSoon {
      background: $red;
      @media screen and (max-width: $tablet-max) {
        margin-top: 0px;
      }
    }
    @media screen and (max-width: $tablet-max) {
      position: relative;
      width: 100%;
      height: initial; // calc(100vh - 65px);
      margin-top: 65px;
      padding-bottom: 196px; // 220px; // account for bottom
      display: none;
      z-index: 6;
      &.showInfluencer {
        display: block;
      }
    }
    >.content {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% - 45px);
      height: 100%; // background: rgba(0, 0, 255, 0.15);
      overflow: auto; //
      @media screen and (max-width: $tablet-max) {
        position: relative;
        width: 100%;
        height: initial;
      }
      >.influencer {
        position: relative; // margin: auto;
        // background: rgba(255, 0, 0, 0.15);
        top: 50%;
        transform: translateY(-50%); //
        @include p1;
        transition: all 0.5s ease-in-out;
        @media screen and (max-width: $tablet-max) {
          top: 0;
          transform: none; // padding-top: 40px;
          padding-top: 10px;
          padding-bottom: 40px;
        }
        >p {
          font-size: 30px;
          position: relative;
          text-align: center;
          text-transform: uppercase;
          padding-bottom: 7vh; // 8vh; // (60px / 800px) canvas in Photoshop
          @media screen and (max-width: $tablet-max) {
            display: none; // padding-bottom: 24px;
          }
        }
        >.image {
          position: relative;
          margin: auto;
          width: 60%;
          @include aspect-ratio(1, 1); // background: rgba(0, 0, 255, 0.4);
          @media screen and (max-width: $mobile-max) {
            // width: 80%;
            // width: 47vh;
            width: 40vh;
          }
          @include translate-up(0.8s, 0.5s, 15%);
          >.influencerImage {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $extra-light-red;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;

          }
        }
        >.influencer-title {
          position: relative;
          top: calc(-50% + 4vh);
          transform: translateY(calc(-50% + 4vh)); // account for padding / 2
          >h1 {
            // @keyframes removeDelay {
            //   to {
            //     transition-delay: 0s !important;
            //   }
            // }
            // todo: move to fonts
            position: relative;
            text-align: center; // font-size: calc(24px * 3.45);
            font-size: 62.8px;
            font-family: "Playfair Display";
            text-transform: capitalize;
            cursor: pointer;
            padding-left: 12px;
            padding-right: 12px;
            @include translate-up(0.75s, 0.75s); // transition-delay: 0s !important;
            @media screen and (max-width: $tablet-max) {
              display: none
            }
          }
          >.shop-button {
            // todo: make a component or a mixin
            position: relative;
            box-sizing: border-box;
            text-align: center;
            padding-top: 8vh; // (60px / 800px) canvas in Photoshop
            @include translate-up(0.75s, 0.75s);
            >span {
              @include p1;
              padding: 12px;
              border: 1px solid $white;
              background: $black;
              color: $white;
              box-sizing: border-box;
              cursor: pointer;
              text-align: center;
              text-transform: uppercase;
              position: relative;
              z-index: 1;
              overflow: hidden;

              &::after {
                content: 'shop this look';
                color: $black;
                position: absolute;
                text-align: center;
                box-sizing: border-box;
                top: 0;
                left: 0;
                width: 100%;
                text-transform: uppercase;
                margin-top: 12.5px;
                z-index: 2;
                transition-delay: 0.4s;
                transition: all 0.4s ease-in-out;
                opacity: 0;
              }

              &::before {
                position: absolute;
                content: ' ';
                top: 0;
                right: 0;
                width: 0%;
                height: 100%;
                background: $white; //rgba(0, 0, 0, 0.3);
                z-index: 1;
                transition: all 0.4s ease-in-out;
                transform-origin: 100% 50% !important;
              }

              // take out hover on tablet/mobile iOS fix
              @media screen and (min-width: $desktop-min) {
                &:hover {
                  border: 1px solid $black;
                  &::before {
                    right: initial;
                    left: 0;
                    width: 100%;
                  }
                  &::after {
                    opacity: 1;
                    transition-delay: 0s;
                  }
                }
              }

            }
          }


        }
      }
    }
    >.contentComingSoon {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% - 45px);
      height: 100%; // background: rgba(0, 0, 255, 0.15);
      overflow: auto; //
      @media screen and (max-width: $tablet-max) {
        position: relative;
        width: 100%;
        height: calc(100vh - 196px); // calc(100vh - 220px);
      }
      >div {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin-top: 45px;
        transition: all 0.5s ease-in-out;

        >p {
          @include p1;
          position: relative;
          text-align: center;
          text-transform: uppercase;
          color: $utility-red;
          margin-top: 12px;
        }
        >h1 {
          // todo: move to fonts
          position: relative;
          text-align: center;
          font-size: calc(24px * 3.45);
          font-family: "Playfair Display";
          text-transform: capitalize;
          color: $utility-red;
          @media screen and (max-width: $mobile-max) {
            // font-size: 24px;
            // margin: 12px;
            font-size: calc(16px * 3.45);
          }
        }
      }
    }
    >.return-bar {
      position: absolute;
      top: 0;
      right: 0;
      width: 45px;
      height: 100%; // background: rgba(255, 0, 0, 0.7);
      background: $black;
      cursor: pointer;
      z-index: 2;
      @media screen and (max-width: $tablet-max) {
        position: fixed;
        height: 65px;
        width: 100%;
        background: $black;
        margin-left: 0px;
        z-index: 7;
      }
      p {
        @include p1; // https://stackoverflow.com/questions/15138723/vertically-center-rotated-text-with-css
        // todo: make this a mixin
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%) rotate(-90deg);
        text-transform: uppercase;
        word-wrap: none;
        user-select: none;
        width: 250px;
        text-align: center;
        color: white;
        opacity: 0.85;
        transition: opacity 0.3s ease-in-out;
        @media screen and (max-width: $tablet-max) {
          opacity: 1;
          top: 0;
          left: 0;
          right: 0;
          margin: auto;
          transform: none;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      &:hover {
        p {
          opacity: 1;
        }
      }
    }
    .playButton {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      width: 72px;
      height: 72px;
      cursor: pointer;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.25);
      transition: all $transition-1;
      &::after {
        content: ' ';
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        margin-left: 24px;
        width: 0px;
        height: 0px;
        border-top: 18px solid transparent;
        border-bottom: 18px solid transparent;
        border-left: 30px solid $white;
      }
      &:hover {
        background: rgba(255, 255, 255, 0.35);
        box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.1);
      }
    }
  } // todo: make reuseable (copied from sidebar scss)
  .refineryLogo {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 20px; // width: 100%;
    height: 45px !important; // background: rgba(255, 0, 0, 0.25);
    width: 35px !important;
    margin-left: 15px !important;
    cursor: pointer; // @media screen and (max-width: $tablet-max) {
    //   width: 45px;
    //   margin-top: 0px;
    //   top: 50%;
    //   transform: translateY(-50%);
    //   margin-left: 20px;
    //   >svg {
    //     fill: white;
    //   }
    // }
    @media screen and (min-width: $desktop-min) {
      display: none;
    }
  }

  .closeIcon {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 20px;
    width: 45px;
    height: 45px;
    @media screen and (max-width: $tablet-max) {
      // margin-right: 75px !important;
      display: none;
      &.videoVisible {
        display: block;
        margin-right: 10px !important;
      }
    }
  }

  .shareIcon {
    position: absolute;
    top: 0;
    right: 0; // width: 60px;
    width: 45px !important;
    height: 100% !important; // margin-right: 75px !important; // background: blue;
    @media screen and (min-width: $desktop-min) {
      display: none;
    }
    >svg {
      transform: translate(2px, -0.5px);
    }
  }


  .refineryLogo,
  .closeIcon,
  .shareIcon {
    >svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.8;
      transition: opacity 0.3s ease-in-out;
      @media screen and (max-width: $tablet-max) {
        opacity: 1;
      }
    }
    &:hover {
      >svg {
        opacity: 1;
      }
    }
    @media screen and (max-width: $tablet-max) {
      width: 45px;
      margin-top: 0px;
      top: 50%;
      transform: translateY(-50%);
      margin-left: 20px;
      margin-right: 20px;
      >svg {
        fill: white;
      }
    }
  }

  .videoContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: auto;
    background: $black;
    z-index: 1;
  } // .jwVideo {
  //   position: relative; // overflow: hidden;
  //   @media screen and (max-width: $tablet-max) {
  //     top: 50%;
  //     transform: translateY(-50%);
  //   }
  // }
  .shareOptionsContainer {
    position: relative;
    margin-right: 8px;
  }

</style>

<style lang="scss">
  @import '../scss/variables'; // style jwplayer element
  .jwplayer {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 129px);
    margin-left: 84px;
    max-height: 100vh;
    @media screen and (max-width: $tablet-max) {
      margin-left: 0px;
      width: 100%;
      margin-top: 65px;
      height: calc(100vh - 65px);
    } // &.jw-flag-fullscreen {
    //   margin-left: 0px !important;
    // }
  }

</style>



