<template>
  <div class="left-container">
    <div class="content">
      <div class="list">

        <div class="revlonContainer">
          <revlonStack />
        </div>

        <!-- Slider main container -->
        <div class="swiper-container influencer-swiper" :class="influencerVisibleSwiperClass">

          <!-- Additional required wrapper -->
          <div class="swiper-wrapper" :class="[listAnimationClass]">

            <div class="list-item swiper-slide" v-for="(item, index) in list.slice(0, 3)" v-bind:key="index" @click="showInfluencer(item)"
              @mouseenter="hoverMapState(item.locationName)" @mouseleave="hoverMapState(item.locationName, false)" :data-listLocation="item.locationName"
              :data-slideIndex="index" :data-routename="makeUrl(item.influencerName)" :style="computeListDelay(index)" :class="item.status">
              <div class="inner" :class="getActiveLocation(item.locationName)">
                <div class="image" :style="influencerImage(item.influencerImageUrl)"></div>
                <div class="copy">
                  <p>{{item.locationName}}</p>
                  <h1>{{item.influencerName}}</h1>
                  <p v-if="item.status === 'soon'" class="nextStopMobile">next stop</p>
                </div>
                <div class="comingSoon" v-if="item.status === 'soon'">
                  <p>next stop</p>
                </div>
                <arrowRight />
              </div>
            </div>

            <!-- AD UNIT DESKTOP -->

            <div class="adUnit desktop" id="div-gpt-ad-1521493540833-1"></div>

            <!--  -->

            <div class="list-item swiper-slide" v-for="(item, index) in list.slice(3)" v-bind:key="(index + 3)" @click="showInfluencer(item)"
              @mouseenter="hoverMapState(item.locationName)" @mouseleave="hoverMapState(item.locationName, false)" :data-listLocation="item.locationName"
              :data-slideIndex="index + 3" :style="computeListDelay(index + 3)" :class="item.status">
              <div class="inner" :class="getActiveLocation(item.locationName)">
                <div class="image" :style="influencerImage(item.influencerImageUrl)"></div>
                <div class="copy">
                  <p>{{item.locationName}}</p>
                  <h1>{{item.influencerName}}</h1>
                  <p v-if="item.status === 'soon'" class="nextStopMobile">next stop</p>
                </div>
                <div class="comingSoon" v-if="item.status === 'soon'">
                  <p>next stop</p>
                </div>
                <arrowRight />
              </div>
            </div>

          </div>

          <!-- navigation buttons -->
          <div class="swiper-button-prev">
            <arrowRight />
          </div>
          <div class="swiper-button-next">
            <arrowRight />
          </div>
        </div>


        <!-- AD UNIT MOBILE -->
        <div class="adUnit mobile" id="div-gpt-ad-1521493540833-0" v-if="isDesktop"></div>
        <!--  -->

      </div>
    </div>
  </div>
</template>

<script>
  import arrowRight from '@/components/arrowRight'
  import revlonStack from '@/components/revlonStack'
  import swiper from 'Swiper'
  import smoothScroll from '../js/smoothScroll'


  // todo: refactor so these methods aren't used twice on index.vue and here
  // todo: make these imports at the very least.

  const getMapEl = (location, options) => {
    options = options || {}

    let commaInd = location.indexOf(", ")
    let mapState = location.slice(commaInd).replace(", ", "")
    if (commaInd === -1) mapState = location.toLowerCase().split(' ').join('')
    let mapEl = document.querySelector('.' + mapState)

    if (options.stringOnly) return mapState

    return mapEl
  }

  const eastStates = ["ME", "NH", "VT", "MA", "RI", "CT", "NY", "PA", "DE", "MD", "WV", "VA",
    "NC", "SC", "GA", "FL",
    "OH", "KY", "TN", "AL", "MS", "MI", "IL", "IN"
  ]

  //

  export default {
    props: ["list", "slideIndex", "influencerVisible", "listAnimationClass"],
    data() {
      return {
        swiper: null,
        sortedList: [],
        activeLocation: "",
        // window: null
        // hasAnimationClass: ""
      }
    },
    methods: {
      makeUrl(str) {
        return str.split(' ').join('-').toLowerCase()
      },
      isDesktop() {
        return true
        // return this.window.innerWidth > 995
      },
      influencerImage(imageUrl) {
        return "background-image: url('" + imageUrl + "')"
      },
      showInfluencer(item) {

        // close influencer or do nothing if already selected
        if (this.activeLocation === item.locationName) {
          // this.$emit('hideInfluencer')
          return
        }

        this.$emit('showInfluencer', item)
        this.activeLocation = item.locationName
        // scroll to selected element
        smoothScroll(document.querySelector("[data-listLocation='" + item.locationName + "']"), 900, null, document.querySelector(
          '.left-container'), -12)
      },
      getActiveLocation(str) {
        return str === this.activeLocation ? "hovered" : ""
      },
      hoverMapState(location, bool) {


        if (window.innerWidth < 996) return // only hover state on desktop

        bool = bool || true

        // san francisco hover
        if (location.toLowerCase().indexOf('luis') !== -1) {
          // location = 'San Luis Obispo, CA'
          location = "Fullerton, CA"
        }

        let mapEl = document.querySelector("[data-mapLocation='" + location + "']")

        if (!mapEl) return

        let mapClass = mapEl.classList
        if (!mapClass.contains('hovered-desktop') && bool) {
          mapClass.add('hovered-desktop')
        } else {
          mapClass.remove('hovered-desktop')
        }

      },
      computeListDelay(delay) {
        return "animation-delay: " + (delay * 125) + "ms"
      },
      // toggleSwiperEnabled () {
      //   // disable swiper behavior on desktop
      //   if (!this.swiper) return

      //   if (window.innerWidth > 995) {
      //     console.log('disabling')
      //     this.swiper.detachEvents()
      //   } else {
      //     console.log('re enabling')
      //     this.swiper.attachEvents()
      //   }

      // }
    },
    components: {
      arrowRight,
      revlonStack
    },
    mounted() {
      setTimeout(() => {
        googletag.display('div-gpt-ad-1521493540833-0')
        googletag.display('div-gpt-ad-1521493540833-1')
      }, 4000)


      // window.addEventListener('resize', this.toggleSwiperEnabled)
    },
    computed: {
      influencerVisibleSwiperClass() {
        return this.influencerVisible ? 'influencerVisible' : ''
      }
    },
    watch: {
      list() {

        let ctx = this
        // init swiper on list populate
        setTimeout(() => {
          this.swiper = new swiper('.influencer-swiper', {
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            },
            on: {
              slideChangeTransitionStart() {

                // click current slide if viewing influencers already
                if (ctx.influencerVisible) document.querySelector('.swiper-slide-active').click()

                // hover current state
                let listLocation = document.querySelector('.swiper-slide-active').getAttribute(
                  'data-listLocation')
                // handle SF
                if (listLocation.toLowerCase().indexOf('luis') !== -1) {
                  // listLocation = 'San Luis Obispo, CA'
                  listLocation = "Fullerton, CA"
                }
                let mapEl = document.querySelector("[data-mapLocation='" + listLocation + "']")
                if (!mapEl) return

                // unhover other states
                let allMapElements = document.querySelectorAll("[data-mapLocation]")
                for (var i = 0; i < allMapElements.length; i++) {
                  allMapElements[i].classList.remove('hovered')
                }

                // hover current state
                let mapClass = mapEl.classList
                mapClass.add('hovered')

                // move map to appropriate side on mobile
                // todo: refactor so these methods aren't used twice on index.vue and here
                let mapState = getMapEl(listLocation, {
                  stringOnly: true
                })
                if (eastStates.indexOf(mapState) !== -1) {
                  ctx.$emit("changeMapTranslateClass", "mapTranslated")
                } else {
                  ctx.$emit("changeMapTranslateClass", "")
                }

              }
            }
          })


          // swipe to 0 after initialize
          setTimeout(() => {
            if (this.swiper) {
              this.swiper.slideTo(1, 1)
              this.swiper.slideTo(0, 1)
            }
          })


        })
      },
      slideIndex() {
        // move slide when map clicked
        if (this.swiper) this.swiper.slideTo(this.slideIndex, 400)
      },
      influencerVisible() {
        if (!this.influencerVisible) this.activeLocation = ""
      }
    }
  }

</script>


<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/swiper';
  @import '../scss/fonts';

  .left-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    box-sizing: border-box; // background: rgba(255, 0, 0, 0.25);
    background: $red;
    overflow: auto;
    z-index: 2;
    @media screen and (max-width: $tablet-max) {
      // position: relative;
      position: fixed;
      top: initial;
      bottom: 0;
      width: 100%;
      // height: 196px; // background: white;
      height: 27vh;
      overflow: hidden; // todo: minor, fix later. ad unit causes small overflow
      z-index: 7;
    }
    >.content {
      position: absolute;
      top: 0;
      right: 0;
      width: calc(100% - 24px); // background: rgba(0, 0, 255, 0.15);
      @media screen and (max-width: $tablet-max) {
        position: relative;
        width: 100%;
      }
    }
  }

  .list {
    position: relative;
    margin-top: 24px;
    margin-left: auto;
    margin-right: auto;
    width: 69%; // background: rgba(255, 0, 0, 0.25);
    @media screen and (max-width: $tablet-max) {
      width: 100%;
      margin-top: 0px;
    }
  }

  @keyframes translate-up-animation {
    from {
      opacity: 0;
      transform: translateY(25%);
    }
    to {
      opacity: 1;
      transform: none;
    }
  }

  .list-item {
    position: relative;
    box-sizing: border-box;
    padding-top: 16px;
    padding-left: 36px; // padding-bottom: 16px;
    padding-bottom: 0px;
    border-bottom: 1px solid $utility-red; // background: rgba(0, 0, 255, 0.15);
    cursor: pointer;

    opacity: 0;
    @media screen and (max-width: $tablet-max) {
      border-bottom: none;
      display: flex;
      justify-content: center;
    }

    >.inner {
      position: relative;
      @media screen and (min-width: $desktop-min) {
        margin-bottom: 16px;
      }
      @media screen and(max-width: $tablet-max) {
        // width: 80%;
        // margin: auto;
        // background: blue;
        display: inline-block;
        padding-right: 50px; // 60 without image
        margin: auto;
      } //
      >.image {
        position: relative;
        box-sizing: border-box;
        display: inline-block;
        vertical-align: middle;
        width: 100px;
        height: 100px; // background: rgba(0, 0, 255, 0.25);
        max-width: 100px;
        max-height: 100px;
        background-color: $light-red; // display: none; // without image
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        @media screen and (max-width: $tablet-max) {
          width: 17vw;
          height: 10vh;
        }
      }
      >.copy,
      .comingSoon {
        position: relative;
        box-sizing: border-box;
        display: inline-block;
        vertical-align: middle; // text-align: center; // without image
        padding-left: 24px; // background: rgba(255, 0, 0, 0.15);
        max-width: calc(100% - 124px);
        @media screen and (max-width: $tablet-max) {
          max-width: initial;
        } // @media screen and (max-width: $mobile-max) {
        //   max-width: calc(100% - 200px);
        // }
        >p {
          @include p1;
          font-size: 8px;
        }
        >h1 {
          @include h11;
          text-transform: capitalize;
          font-size: 27px;
          @media screen and (max-width: $tablet-max) {
            font-size: 20.45705px;
          }
        }
        p,
        h1 {
          transition: all $transition-1;
        }
        .nextStopMobile {
          @media screen and (min-width: $desktop-min) {
            display: none;
          }
        }
      }
      >.comingSoon {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        margin-right: 36px; // >p {
        //   padding-left: 36px
        // }
        @media screen and (max-width: $tablet-max) {
          display: none;
        }
      }
      
      >svg {
        position: absolute;
        overflow: visible;
        top: 50%;
        right: 0;
        transform: translateY(-50%) scale(1.5, 1.5);
        transition: all $transition-1;
        @media screen and (max-width: $tablet-max) {
          display: none;
        }
      }
      &:hover,
      &.hovered {
        >svg {
          transform: translateY(-50%) scale(1.5, 1.5) translateX(4px);
          fill: $extra-light-red;
        }
        >.copy {
          p,
          h1 {
            color: $extra-light-red;
          }
        }
      }
    } // copy disabled class
    &.soon {
      @media screen and (min-width: $desktop-min) {
        pointer-events: none;
      }
      >.inner {
        * {
          color: $utility-red;
        }
        >.image {
          width: 0px;
        }
        >.copy {
          padding-left: 0px;
        }
        &.hovered {
          @media screen and (max-width: $tablet-max) {
            display: none;
          }
        }
      }

    }

  } // swiper style overrides
  .swiper-container {
    &.influencerVisible {
      @media screen and (max-width: $tablet-max) {
        // pointer-events: none;
        // .swiper-button-next,
        // .swiper-button-prev {
        //   // opacity: 0;
        // }
      }
    }
  }

  .swiper-wrapper {
    &.hasAnimation {
      >.list-item {
        animation: translate-up-animation 0.5s forwards;
      }
    }
    @media screen and (min-width: $desktop-min) {
      display: block;
      transform: none !important;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    background-image: none;
    user-select: none;
    outline: 0 !important;
    @media screen and (min-width: $desktop-min) {
      display: none;
    }
    >svg {
      position: absolute;
      top: 50%;
      transform: translateY(-50%) scale(1.5, 1.5);
    }
  }

  .swiper-button-prev {
    >svg {
      transform: scaleX(-1) translateY(-50%) scale(1.5, 1.5);
    }
  }

  .swiper-button-disabled {
    opacity: 0 !important;
  }

  .adUnit {
    position: relative;
    margin: auto; // background: rgba(255, 0, 0, 0.45);
    // background: $light-red;
    width: 350px;
    height: 50px;
    max-width: 100%;
    text-align: center; // margin-top: 20px;
    // margin-bottom: 20px;
    margin-top: 16px;
    overflow: hidden;
    @media screen and (min-width: $desktop-min) {
      width: 300px;
      height: 250px;
      margin-top: 40px;
      margin-bottom: 40px;
    }
    >p {
      @include p1;
      text-transform: capitalize;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      color: white;
      user-select: none;
    }
    &.desktop {
      @media screen and (max-width: $tablet-max) {
        display: none;
      }
    }
    &.mobile {
      @media screen and (min-width: $desktop-min) {
        display: none;
      }
    }
  }

  .revlonContainer {
    position: relative;
    margin-top: 24px;
    margin-bottom: 48px;
    @media screen and (max-width: $tablet-max) {
      display: none;
    }
  }

</style>



