<template>
  <div class="right-container" :class="customClass">
    <div class="content">
      <div class="map">
        <p>Travel around the country to<br /> discover these women's bold hometown-inspired beauty looks.<br /> Click on a state below to start your road trip.</p>
        <div class="mapContainer">
          <usMap :mapClass="mapTranslateClass" @changeSlideIndex="changeSlideIndex" />
        </div>
        <div class="legend-container">
          <div class="liveColor"></div>
          <div class="label">live content</div>
          <div class="soonColor"></div>
          <div class="label">next stop</div>
          <div class="selectionColor"></div>
          <div class="label">selection</div>
        </div>
      </div>
    </div>

    <!-- <mapSwiper :list="list" @showInfluencer="showInfluencer" @changeSlideIndex="changeSlideIndex" /> -->

  </div>
</template>

<script>
  import usMap from '@/components/usMap'
  import mapSwiper from '@/components/mapSwiper'

  export default {
    props: ["mapTranslateClass", "customClass", "list"],
    data () {
      return {
        swiper: null
      }
    },
    methods: {
      showInfluencer (item) {
        this.$emit('showInfluencer', item)
      },
      changeSlideIndex(index) {
        this.$emit("changeSlideIndex", index)
      }
    },
    components: {
      usMap,
      mapSwiper
    }
  }

</script>


<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/fonts';

  .right-container {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    box-sizing: border-box; // background: rgba(255, 0, 0, 0.15);
    background: $white;
    overflow: hidden;
    &.hideMap {
      @media screen and (max-width: $tablet-max) {
        display: none;
      }
    }
    @media screen and (max-width: $tablet-max) {
      position: relative;
      width: 100%;
      height: initial;
      padding-top: 40px;
      padding-bottom: 40px;
    }
    @media screen and (max-width: $mobile-max) {
      padding-bottom: 12px;
      padding-top: 65px;
    }
    >.content {
      position: absolute;
      left: 0;
      height: 100%;
      top: 0;
      width: calc(100% - 24px); // background: rgba(0, 0, 255, 0.15);
      @media screen and (max-width: $tablet-max) {
        position: relative;
        width: 100%;
        height: initial;
      }
      .map {
        position: relative;
        top: 50%;
        transform: translateY(-50%); // background: rgba(255, 0, 0, 0.4);
        @media screen and (max-width: $tablet-max) {
          top: 0;
          transform: none;
          margin-top: 65px;
          padding-top: 20px;
          padding-bottom: 196px; // 220px; // account for nav
        }
        @media screen and (max-width: $mobile-max) {
          // margin-top: 65px;
          margin-top: 0px;
          padding-top: 0px;
        }
        >p {
          position: relative;
          text-align: center;
          text-transform: uppercase;
          user-select: none;
          padding-bottom: 12vh;
          @include p1;
          line-height: 2;
          @media screen and (max-width: $tablet-max) {
            display: none;
          }
        }
        >.mapContainer {
          position: relative;
          width: 100%;
          overflow: hidden;
          @media screen and (max-width: $mobile-max) {
            // shrink svg container, grow svg inside
            height: calc(100vh - 306px); // calc(100vh - 330px);
          }
          >svg {
            position: relative; // top: 0;
            height: 100%;
            width: 85%;
            left: 50%;
            transform: translateX(-50%); // margin: auto; // height: 100%;
            transition: all $transition-2;
            @media screen and (max-width: $mobile-max) {
              height: calc(100% - 4px);
              width: 100%;
              top: 0;
              left: 0;
              margin-top: 4px;
              width: calc(100% - 24px);
              margin-left: 12px;
              transform: none;
              // transform: scale(1.4, 1.4) translateX(20%);
              // &.mapTranslated {
              //   transform: scale(1.4, 1.4) translateX(-10%);
              // }
            }
          }
        }

        >.legend-container {
          position: relative;
          text-align: center;
          user-select: none;
          padding-top: 12vh;
          margin-left: 24px;
          @media screen and (max-width: $mobile-max) {
            padding-top: 12px;
            margin-left: 6px;
          }
          .liveColor,
          .soonColor,
          .selectionColor {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: 16px;
            height: 16px;
          }
          .liveColor {
            background: $light-red;
          }
          .soonColor {
            background: $extra-light-red
          }
          .selectionColor {
            background: $red
          }
          .label {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-size: 12.5px;
            font-family: "BrownStd";
            font-weight: bold;
            line-height: 1.667;
            text-transform: capitalize;
            margin-right: 24px;
            @media screen and (max-width: $mobile-max) {
              margin-right: 6px;
            }
          }
        }
      }

    }
  }

</style>



