<script>
  export default {
    props: ["mapClass"],
    data() {
      return {
        sfHovered: false
      }
    },
    methods: {
      hoverSF() {
        this.sfHovered = true
      },
      leaveSF() {
        this.sfHovered = false
      },
      clickSF() {
        // manually click SF
        let sfList = document.querySelector("[data-listLocation='San Luis Obispo, CA']")
        // console.log('clicking sf')
        // console.log(sfList)
        if (sfList) {
          sfList.click()
          this.$emit("changeSlideIndex", sfList.getAttribute('data-slideIndex'))
        }
      }
    },
    computed: {
      CAClass() {
        return this.sfHovered ? 'hovered hovered-desktop' : ''
      }
      // add hover style here
    },
  }

</script>


