<template>
  <div class="landing" :class="landingClass">

    <div class="refineryLogo">
      <refineryLogoStack />
    </div>

    <h1>Live Boldly</h1>
    <h1>Road Trip</h1>

    <h2>Got wanderlust? Us too. We traveled around the country with Revlon  from Cali to Kentucky  to see how locals are living
      their best and boldest lives via their hometown-inspired beauty looks.</h2>



    <div class="paidForWrap">
      <revlonStack />
    </div>

    <div class="jumpIn" @click="hideLanding">
      <span>jump in</span>
    </div>

    <div class="copyright">
      <p> 2018 REFINERY29</p>
      <p>BY USING OUR SERVICE, YOU AGREE TO OUR TERMS (EFFECTIVE 2/07/2017) AND PRIVACY (EFFECTIVE 2/07/2017).</p>
      <p>REFINERY29 NAME & LOGO AND R29 LOGO ARE TRADEMARKS OF REFINERY 29 INC.</p>
    </div>

    <div class="transitionWhite" :class="transitionBlockClass"></div>
    <div class="transitionRed" :class="transitionBlockClass"></div>

    <div class="landingOverlay" :class="overlayClass"></div>

  </div>
</template>

<script>
  import refineryLogoStack from './refineryLogoStack'
  import revlonStack from './revlonStack'
  // import revlonLogo from './revlonLogo'


  export default {
    props: ['landingWatcher'],
    data() {
      return {
        landingClass: 'landingNoScroll',
        transitionBlockClass: '',
        overlayClass: ''
      }
    },
    methods: {
      hideLanding() {

        gtag('event', 'clicked', {
          'event_label': 'intro button',
          'event_category': 'Bespoke - Revlon Map'
        })

        if (this.landingClass === "landingHidden") return

        document.querySelector('.landing').scrollTop = 0 // (0, 0) // todo: add polyfill for IE11
        this.transitionBlockClass = 'showTransition'
        setTimeout(() => {
          this.landingClass = "landingInvisible"
        }, 500)
        // 

        setTimeout(() => {
          this.landingClass = 'landingHidden'
          this.$emit('jumpIn')
          gtag('event', 'visible', {
            'event_label': 'map',
            'event_category': 'Bespoke - Revlon Map'
          })
        }, 1000)
      }
    },
    mounted() {

      // gtag
      gtag('event', 'visible', {
        'event_label': 'intro',
        'event_category': 'Bespoke - Revlon Map'
      })
      //

      setTimeout(() => {
        this.overlayClass = "overlayHidden"
        this.landingClass = ""
      }, 250)
      // PUT THIS BACK WHEN DONE
      setTimeout(() => {
        this.hideLanding()
      }, 5750)
    },
    watch: {
      // landingWatcher() {
      //   // if (this.landingWatcher) {
      //   //   // this.hideLanding()
      //   // }
      // }
    },
    components: {
      refineryLogoStack,
      revlonStack
      // revlonLogo
    }
  }

</script>

<style scoped lang="scss">
  @import '../scss/variables';
  @import '../scss/fonts';

  .landing {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    min-height: 600px;
    background: $red;
    z-index: 99;
    overflow: auto;
    transition: all 0.5s ease-in-out;
    &.landingNoScroll {
      overflow: hidden;
    }
    &.landingInvisible {
      opacity: 0;
    }
    &.landingHidden {
      display: none;
    }

    >h1 {
      font-size: calc(7.317px * 21/*27.43*/
      );
      font-family: "BrownStd";
      color: $white;
      font-weight: bold;
      line-height: 0.954;
      text-align: center;
      @media screen and (max-width: $tablet-max) {
        font-size: 60px;
      }
    }
    >h2 {
      font-size: 14.583px;
      font-family: "Playfair Display";
      color: $black;
      line-height: 1.857;
      text-align: center; // position: absolute;
      position: relative; // top: 0;
      // left: 0;
      // right: 0;
      // margin-top: 360px;
      width: 350px;
      max-width: calc(100% - 24px);
      margin: auto;
      margin-top: 40px; // margin-top: 20em;
      @media screen and (max-width: $tablet-max) {
        position: relative;

      }
    }

    .refineryLogo {
      position: relative;
      margin: auto;
      width: 100px;
      height: 72px; // margin-top: 60px; // todo: vh
      margin-top: 25px;
      margin-bottom: 40px; // 90px;
      >svg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
      }
      @media screen and (max-width: $tablet-max) {
        margin-top: 48px;
        margin-bottom: 24px;
      }
    }

    .paidForWrap {
      position: relative;
      margin-top: 40px;
    } // .revlonStack {
    //   position: relative;
    //   font-size: 11px;
    //   letter-spacing: 2.2px;
    //   font-family: "BrownStd";
    //   color: $black;
    //   font-weight: bold;
    //   text-transform: uppercase;
    //   text-align: center;
    //   >.liveBoldly {
    //     @include live-boldly;
    //   }
    // }
    // .revlon {
    //   position: relative;
    //   display: inline-block;
    //   vertical-align: bottom;
    //   height: 24px;
    //   width: 96px;
    //   padding-left: 6px;
    //   >svg {
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     width: 100%;
    //     transform: translateY(-10px) scale(2, 2)
    //   }
    // }
    .copyright {
      position: relative;
      @media screen and (max-width: $tablet-max) {
        padding: 12px;
      }
      >p {
        font-size: 9.375px;
        font-family: "BrownStd";
        color: $white;
        opacity: 0.5;
        line-height: 1.556;
        text-align: center;
      }
    }

    .jumpIn {
      position: relative;
      text-align: center;
      box-sizing: border-box;
      margin: 12px;
      padding: 50px;
      >span {
        padding: 12px;
        border: 1px solid $black;
        cursor: pointer;
        text-align: center;
        text-transform: uppercase;
        position: relative;
        overflow: hidden;
        letter-spacing: 2.2px;
        &::after {
          content: 'jump in';
          color: $white;
          position: absolute;
          text-align: center;
          box-sizing: border-box;
          top: 0;
          left: 0;
          width: 100%;
          text-transform: uppercase;
          margin-top: 12.5px;
          z-index: 2;
          transition-delay: 0.4s;
          transition: all 0.4s ease-in-out;
          opacity: 0;
        }

        &::before {
          position: absolute;
          content: ' ';
          top: 0;
          right: 0;
          width: 0%;
          height: 100%;
          background: $black; //rgba(0, 0, 0, 0.3);
          z-index: 1;
          transition: all 0.4s ease-in-out;
          transform-origin: 100% 50% !important;
        }

        // iOS fix
        @media screen and (min-width: $desktop-min) {
          &:hover {
            &::before {
              right: initial;
              left: 0;
              width: 100%;
            }
            &::after {
              opacity: 1;
              transition-delay: 0s;
            }
          }
        }

      }
    } // .bottomContent {
    //   position: absolute;
    //   bottom: 0;
    //   left: 0;
    //   right: 0;
    //   margin: auto;
    // }
  }

  .transitionWhite {
    position: absolute;
    top: 0;
    right: 0;
    width: 0px;
    height: 100%;
    background: white;
    transition: all 0.5s ease-in-out;
    &.showTransition {
      width: 50%;
    }
    @media screen and (max-width: $tablet-max) {
      display: none !important;
    }
  }

  .transitionRed {
    position: absolute;
    top: 0;
    left: 0;
    width: 0px;
    height: 100%;
    background: $red;
    transition: all 0.5s ease-in-out;
    @media screen and (max-width: $tablet-max) {
      width: 100%;
      height: 0%;
    }
    &.showTransition {
      width: 50%;
      @media screen and (max-width: $tablet-max) {
        width: 100%;
        height: 100%;
        z-index: 6;
      }
    }

  }

  .landingOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $red;
    transition: all 0.5s ease-in-out;
    opacity: 1;
    pointer-events: none;
    &.overlayHidden {
      opacity: 0;
    }
  }

</style>



